.Item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid #ccc;
    padding: 10px;
}

.Item-image {
    width: 64px;
    height: 64px;
    border: 1px solid #ccc;
    margin-right: 10px;
    float: left;
}
.Item-image>img {
    width: 64px;
    height: 64px;
}
.Item-title {
    font-weight: bold;
    margin-bottom: .4em;
}

.Item-price {
    text-align: right;
    font-size: 18px;
    color: #555;
}

.Item-addToCart {
    margin-bottom: 5px;
    font-size: 14px;
    border: 2px solid #FFAA3F;
    background-color: #fff;
    border-radius: 3px;
    cursor: pointer;
}

.Item-addToCart:hover {
    background-color: #FFDDB2;
}

.Item-addToCart:active {
    background-color: #ED8400;
    color: #fff;
    outline: none;
}

.Item-addToCart:focus {
    outline: none;
}

.Item-left {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.Item-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}


